@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
* {
    box-sizing: border-box;
}

body {
    padding: 0;
    margin: 0;
    font-family: "Major Mono Display", monospace;
    line-height: 1.5;
}

.app {
    min-height: 100vh;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #87d7cc;
    color: #2c888d;
    position: relative;
}

.info {
    font-size: 20px;
    color: #87d7cc;
    background-color: #1f6e72;
    font-family: monospace;
    display: inline-block;
    width: 28px;
    height: 28px;
    line-height: 28px;
    text-align: center;
    border-radius: 50%;
    position: absolute;
    right: 10px;
    top: 10px;
    cursor: pointer;
}

h2 {
    color: #49b2b8;
    font-size: 30px;
    text-align: center;
    margin: 0;
}

.timer {
    color: #1f6e72;
    font-size: 150px;
    margin-bottom: 20px;
}

.buttons button {
    outline: none;
    border: none;
    font-size: 24px;
    background: #f7fca0;
    color: #878b32;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    padding: 15px 30px;
    cursor: pointer;
    transition: 0.3s ease all;
}

.buttons button:not(:last-child) {
    margin-right: 10px;
}

.options {
    margin-bottom: 40px;
}

.options button {
    border: none;
    font-size: 24px;
    padding: 4px 10px;
    border-radius: 5px;
    margin: 0 5px;
    color: #1f6e72;
    background-color: transparent;
    cursor: pointer;
    outline: none;
}

.options button.active {
    background-color: #1f6e72;
    color: #87d7cc;
}

@media (max-width: 780px) {
    h2 {
        font-size: 22px;
    }

    .timer {
        font-size: 80px;
    }

    .buttons button {
        font-size: 18px;
    }

    .options button {
        font-size: 16px;
    }
}

.popup {
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s ease-in-out;
    transform: scale(1.3);
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(21, 17, 17, 0.61);
    display: flex;
    align-items: center;
}

.popup.show {
    visibility: visible;
    opacity: 1;
    transform: scale(1);
}

.popup-content {
    background-color: #fefefe;
    margin: auto;
    padding: 30px 20px;
    border: 1px solid #888;
    width: 50%;
    position: relative;
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    font-family: monospace;
}

.popup p {
    margin: 0;
}

.popup .author {
    font-style: italic;
    font-weight: 700;
    margin-top: 10px;
}

.popup-close {
    position: absolute;
    top: 10px;
    right: 10px;
    text-transform: lowercase;
    font-weight: bold;
    font-size: 20px;
    display: block;
    line-height: 20px;
    cursor: pointer;
}

.popup-close svg {
    width: 15px;
    height: 15px;
    fill: #1f6e72;
}

@media (max-width: 780px) {
    .popup-content {
        width: 90%;
    }
}

