@import url("https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap");

* {
    box-sizing: border-box;
}

body {
    padding: 0;
    margin: 0;
    font-family: "Major Mono Display", monospace;
    line-height: 1.5;
}

.app {
    min-height: 100vh;
    padding-left: 15px;
    padding-right: 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: #87d7cc;
    color: #2c888d;
    position: relative;
}

.info {
    font-size: 20px;
    color: #87d7cc;
    background-color: #1f6e72;
    font-family: monospace;
    display: inline-block;
    width: 28px;
    height: 28px;
    line-height: 28px;
    text-align: center;
    border-radius: 50%;
    position: absolute;
    right: 10px;
    top: 10px;
    cursor: pointer;
}

h2 {
    color: #49b2b8;
    font-size: 30px;
    text-align: center;
    margin: 0;
}

.timer {
    color: #1f6e72;
    font-size: 150px;
    margin-bottom: 20px;
}

.buttons button {
    outline: none;
    border: none;
    font-size: 24px;
    background: #f7fca0;
    color: #878b32;
    border-radius: 5px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    padding: 15px 30px;
    cursor: pointer;
    transition: 0.3s ease all;
}

.buttons button:not(:last-child) {
    margin-right: 10px;
}

.options {
    margin-bottom: 40px;
}

.options button {
    border: none;
    font-size: 24px;
    padding: 4px 10px;
    border-radius: 5px;
    margin: 0 5px;
    color: #1f6e72;
    background-color: transparent;
    cursor: pointer;
    outline: none;
}

.options button.active {
    background-color: #1f6e72;
    color: #87d7cc;
}

@media (max-width: 780px) {
    h2 {
        font-size: 22px;
    }

    .timer {
        font-size: 80px;
    }

    .buttons button {
        font-size: 18px;
    }

    .options button {
        font-size: 16px;
    }
}
